CP.")])]),_c('tr',[_c('td',[_c('b',[_vm._v("Importancia.")])]),_c('td',[_vm._v("Sienta las bases legales del marco regulatorio en inocuidad alimentaria y garantiza coherencia y continuidad del sistema.")]),_c('td',[_vm._v("Norma operativa con legitimidad derivada de la ley.")]),_c('td',[_vm._v("Complementan el sistema sanitario con lineamientos técnicos específicos.")])])])])])])])
