},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center mb-0"},[_c('div',{staticClass:"col-md-12 col-lg-10"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-3 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img34.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-9"},[_c('div',{staticClass:"row mb-5 justify-content-center align-items-center bg-color-11 p-4"},[_c('div',{staticClass:"col-lg mb-3 mb-lg-0"},[_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-right"}},[_vm._v("En otras palabras, el decreto conecta la teoría legal con la práctica cotidiana, transformando los principios abstractos de la Ley 9 de 1979 en acciones observables dentro de la cocina. Su cumplimiento garantiza que los alimentos sean manipulados en condiciones seguras, protege la salud del consumidor, reduce riesgos de sanciones legales y promueve una cultura de responsabilidad y profesionalismo en el sector gastronómico.")])]),_c('div',{staticClass:"col-auto col-lg-3"},[_c('figure',[_c('img',{staticClass:"d-none d-lg-flex",attrs:{"src":require("@/assets/curso/temas/tema1/img35.svg"),"data-aos":"zoom-in"}})])])]),_c('div',{staticClass:"row mb-0 justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-3 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img36.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-9"},[_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-right"}},[_vm._v("Además, este decreto sirve como base para la implementación de sistemas de inocuidad más avanzados, como el HACCP, ya que establece las condiciones mínimas necesarias sobre las cuales se puede construir un sistema de control de peligros alimentarios más integral. En este sentido, el Decreto 3075 de 1997 no solo regula lo que debe hacerse, sino que también permite que los establecimientos desarrollen procedimientos internos, protocolos de capacitación y controles de calidad que superen los estándares mínimos, fortaleciendo su competitividad y reputación en el mercado.")])])])])])])])
