},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row mb-5 justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-2 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img22.svg"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-10"},[_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-right"}},[_vm._v("Las normas de inocuidad alimentaria solo adquieren sentido cuando existen entidades encargadas de velar por su cumplimiento, ya que, sin vigilancia, los lineamientos legales serían meros documentos sin efecto práctico. En Colombia, la vigilancia y el control sanitario de los alimentos se organiza como un sistema jerárquico y compartido entre una autoridad nacional y las autoridades sanitarias locales, garantizando la cobertura en todo el territorio y el cumplimiento tanto de normas generales como de regulaciones específicas.")])])])
