},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"bloque-texto-g__texto p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("Identificar la diferencia entre el rol del INVIMA y el de las autoridades locales es crucial para los profesionales de la gastronomía. Por ejemplo, cuando un inspector visita una cocina en Medellín, representa a la Secretaría de Salud Municipal, no al INVIMA, y sus observaciones y posibles sanciones se fundamentan en normas de alcance local y nacional. Por otro lado, un producto envasado que requiera Registro Sanitario debe tramitarse ante el INVIMA, siguiendo un proceso técnico y administrativo diferente.")])])
