},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-md-12 col-lg-12"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-2 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img29.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-10"},[_c('p',{staticClass:"mb-2",attrs:{"data-aos":"fade-right"}},[_vm._v("El marco normativo colombiano en materia de seguridad alimentaria no se construyó de manera aislada, sino que se fundamenta en criterios científicos y técnicos ampliamente reconocidos a nivel internacional. Uno de los pilares más importantes de esta normativa es el Codex Alimentarius, un programa conjunto de la Organización de las Naciones Unidas para la Alimentación y la Agricultura (FAO) y la Organización Mundial de la Salud (OMS), cuyo objetivo es desarrollar normas, directrices y códigos de práctica alimentaria armonizados globalmente (FAO & OMS, 2023).")]),_c('div',{staticClass:"row mb-0 justify-content-center align-items-center bg-color-11 p-4"},[_c('div',{staticClass:"col-lg mb-3 mb-lg-0"},[_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-right"}},[_vm._v("El Codex Alimentarius surge como respuesta a la necesidad de proteger la salud de los consumidores y garantizar prácticas equitativas en el comercio internacional de alimentos. Sus normas no solo establecen límites máximos de contaminantes y aditivos, sino que también proporcionan lineamientos sobre etiquetado, higiene, procesamiento y almacenamiento de alimentos. Esto convierte al Codex en un referente técnico y científico que influye directamente en la formulación de políticas y regulaciones nacionales.")])]),_c('div',{staticClass:"col-auto col-lg-2"},[_c('figure',[_c('img',{staticClass:"d-none d-lg-flex",attrs:{"src":require("@/assets/curso/temas/tema1/img30.svg"),"data-aos":"zoom-in"}})])])])])])])])
